import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
} from 'reactstrap';

import CareerImages from 'assets/images/Career';

import CircleHeader from '../Templates/CircleHeader/CircleHeader';
import CareerData from './CareerData';
import JobCategory from './JobCategory';

// Footer
import FooterContainer from '../../containers/FooterContainer';

import './Career.scss';

import {
  onPageBusiness,
  setStateBusiness,
  onPageMore,
  setStateMore,
  onPageInsurance,
  setStateInsurance,
} from '../../actions/navActions';

class Career extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      activeTab: 1,
      collapse: false,
    };
    this.toggleCollapse = this.toggleCollapse.bind(this);

    const pageState = onPageMore.ACTIVE;
    const resetInsurance = onPageInsurance.NONE;
    const resetBusiness = onPageBusiness.NONE;
    const {
      setPage,
      resetIndicatorInsurance,
      resetIndicatorBusiness,
    } = this.props;
    setPage(pageState);
    resetIndicatorInsurance(resetInsurance);
    resetIndicatorBusiness(resetBusiness);
  }
  componentDidMount() {
    this.toggle(0);
    window.scrollTo(0, 0);
  }
  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab,
      });
    }
  }
  toggleCollapse() {
    this.setState({ collapse: !this.state.collapse });
  }
  renderBenefits() {
    const benefitContainer = CareerData.benefits.contents;
    return (
      <div className="content-section">
        <Row className="content-row">
          <div className="title">Benefits</div>
        </Row>
        <Row className="content-row benefits-wrapper">
          {/* Left Benefits List */}
          <Col xs={11} md={4} className="offset-md-1">
            <ul className="benefits-left">
              {
                _.map(benefitContainer[0].left, (leftBenefits, index) => (
                  <li className="benefits-li mb-4" key={index} dangerouslySetInnerHTML={{ __html: leftBenefits }} />
              ))
            }
            </ul>
          </Col>
          {/* Right Benefits List */}
          <Col xs={11} md={4}>
            <ul className="benefits-right">
              {
                  _.map(benefitContainer[0].right, (rightBenefits, index) => (
                    <li className="benefits-li mb-4" key={index} dangerouslySetInnerHTML={{ __html: rightBenefits }} />
                ))
              }
            </ul>
          </Col>
        </Row>
      </div>
    );
  }
  renderJobs() {
    const jobContainer = CareerData.jobs;
    return (
      <Fragment>
        <Col lg={2} md={8} className="job-header">
          <Nav tabs>
            {
              _.map(jobContainer, (job, index) => {
                const activeStyle = this.state.activeTab === index ? 'active' : null;

                return (
                  <NavItem key={index}>
                    <NavLink
                      id={index}
                      className={activeStyle}
                      onClick={() => { this.toggle(index); }}
                    >
                      <p>{ job.header }</p>
                    </NavLink>
                  </NavItem>
                );
              })
            }
          </Nav>
        </Col>
        <Col lg={6} md={8} className="job-title-wrapper">
          {
            _.map(jobContainer, (job, index) => (
              _.map(job.subs, (jobDesc, i) => (
                <JobCategory
                  index={index}
                  jobTitle={jobDesc}
                  activeTab={this.state.activeTab}
                  key={i}
                />
              ))
            ))
          }
        </Col>
      </Fragment>
    );
  }
  render() {
    return (
      <div className="career">
        <CircleHeader
          imgFront={CareerImages.CareerFrontTop}
          imgBack={CareerImages.CareerBGOverlay}
          title="Career"
          desc={
            <Fragment>
              Do you have a strong sense of ownership?
              Do you relish in challenges? Are you passionate for what PolicyPal is building?
              We are afterall revolutionising the industry here!
              Then come join us and be part of our PolicyPal family!
            </Fragment>
          }
        />
        <div className="content">
          {this.renderBenefits()}
          <div className="content-section">
            <Row className="content-row">
              <div className="title">Current openings</div>
            </Row>
            <Row className="content-row jobsOpening-wrapper">
              {this.renderJobs()}
            </Row>
          </div>
        </div>
        <FooterContainer />
      </div>
    );
  }
}

Career.defaultProps = {
  setPage: null,
  resetIndicatorInsurance: null,
  resetIndicatorBusiness: null,
};

Career.propTypes = {
  setPage: PropTypes.func,
  resetIndicatorInsurance: PropTypes.func,
  resetIndicatorBusiness: PropTypes.func,
};

const mapDispatchToProps = dispatch => ({
  setPage: (pageState) => {
    dispatch(setStateMore(pageState));
  },
  resetIndicatorInsurance: (resetInsurance) => {
    dispatch(setStateInsurance(resetInsurance));
  },
  resetIndicatorBusiness: (resetBusiness) => {
    dispatch(setStateBusiness(resetBusiness));
  },
});

export default connect(null, mapDispatchToProps)(Career);
