import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import { Row } from 'reactstrap';
import PropTypes from 'prop-types';

import ClaimsImages from 'assets/images/Claims';

import './CircleHeader.scss';

class CircleHeader extends Component {
  render() {
    const {
      imgFront,
      imgBack,
      title,
      desc,
    } = this.props;
    return (
      <Fragment>
        <Row noGutters className="background-overlay">
          <div className="circle-container">
            <img src={ClaimsImages.CircleBackground} className="bg-circle" alt="CircleBackground" />
            <img src={imgFront} className="img-front" alt={`PolicyPal-${imgFront}`} />
            <img src={imgBack} className="img-back" alt={`PolicyPal-${imgBack}`} />
          </div>
          <div className="title-container">
            <div className="title">{title}</div>
            <div className="desc">
              {desc}
            </div>
          </div>
        </Row>
      </Fragment>
    );
  }
}

CircleHeader.propTypes = {
  imgFront: PropTypes.string.isRequired,
  imgBack: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  desc: PropTypes.object.isRequired,
};

export default withRouter(CircleHeader);
